/**
*
* @title 
* @description expandedRowRender
*
*/

import React, { Component } from "react";
import Table from "../../src";
import BigData from "../../src/lib/bigData";
const BigDataTable = BigData(Table);
const outColumns = [
  {
    title: "",
    dataIndex: "d",
    key: "d", 
    width:200,
    render(text, record, index) {
      return (
        <a
          href="#"
          onClick={() => {
            alert("" + index + ":" + text);
          }}
        >
          
        </a>
      );
    }
  },
  { title: "", dataIndex: "a", key: "a", width: 250 },
  { id: "123", title: "", dataIndex: "b", key: "b", width: 100 },
  { title: "", dataIndex: "c", key: "c", width: 200 },
  
];
const innerColumns = [
  {
    title: "",
    dataIndex: "d",
    key: "d",
    width:200,
    render(text, record, index) {
      return (
        <a
          href="#"
          onClick={() => {
            alert("" + index + ":" + text);
          }}
        >
          {''+index}
        </a>
      );
    }
  },
  { title: "", dataIndex: "a", key: "a", width: 100 },
  { id: "123", title: "", dataIndex: "b", key: "b", width: 100 },
  { title: "", dataIndex: "c", key: "c", width: 200 },
  
];

const data16 = [ ...new Array(10000) ].map((e, i) => {
    return { a: i + 'a', b: i + 'b', c: i + 'c', d: i + 'd', key: i };
   })





class Demo31 extends Component {
  constructor(props){
    super(props);
    this.state={
      data_obj:{
        0:[
          { a: "", b: "", c: 41, d: "", key: "1" },
          { a: "", b: "", c: 67, d: "", key: "2" }
        ],
        1: [
          { a: "", b: "", c: 41, d: "", key: "1" },
          { a: "", b: "nv", c: 67, d: "", key: "2" }
        ],
      }
    }
  }
  expandedRowRender = (record, index, indent) => {
    let height = 200;
    let innderData = [ ...new Array(100) ].map((e, i) => {
      return { a: index+"-"+ i + 'a', b: i + 'b', c: i + 'c', d: i + 'd', key:  index+"-"+ i };
     })
    return (
      <Table
        
        columns={innerColumns}
        scroll={{y:height}}
        data={innderData} 

      />
    );
  };
  getData=(expanded, record)=>{
    //
    let new_obj = Object.assign({},this.state.data_obj);
    if(expanded){
      if(record.key==='1'){
        new_obj[record.key] = [
          { a: "", b: "", c: 41, d: "", key: "1" },
          { a: "", b: "", c: 67, d: "", key: "2" }
        ]
        this.setState({
          data_obj:new_obj
        })
      }else{
        new_obj[record.key] = [
          { a: "", b: "", c: 41, d: "", key: "1" },
          { a: "", b: "nv", c: 67, d: "", key: "2" }
        ]
        this.setState({
          data_obj:new_obj
        })
      }
    }
  }
  haveExpandIcon=(record, index)=>{
    //iconexpandedRowRender
    if(index == 0){
      return true;
    }
    return false;
  }
  render() {
    return (
      <BigDataTable
        columns={outColumns}
        data={data16}
        onExpand={this.getData}
        expandedRowRender={this.expandedRowRender}
        scroll={{y:350}}
        // defaultExpandedRowKeys={[0,1]}
        title={currentData => <div>: </div>}
        footer={currentData => <div>: </div>}
      />
    );
  }
}

export default Demo31;



