import React, { Component } from 'react';
import Table from '../../src'; 
import dragColumn from '../../src/lib/dragColumn';

import Icon from "bee-icon";

const columns23 = [
  {
    title: "",
    dataIndex: "a",
    key: "a",
    width: '200',
    fixed:'left'
  },
  {
    title: "",
    dataIndex: "b",
    key: "b",
    width: '600'
  },
  {
    title: "",
    dataIndex: "c",
    key: "c",
    width: '200',
    sumCol: true,
    sorter: (a, b) => a.c - b.c
  }, 
  {
    title: "",
    dataIndex: "d",
    key: "d",
    width: 500,
  }
];

const data23 = [
  { a: "", b: "", c: 30,d:'', key: "2" },
  { a: "", b: "", c: 41,d:'', key: "1" },
  { a: "", b: "", c: 25,d:'', key: "31" } , { a: "", b: "", c: 30,d:'', key: "21" },
  { a: "", b: "", c: 41,d:'', key: "11" },
  { a: "", b: "", c: 25,d:'', key: "32" } , { a: "", b: "", c: 30,d:'', key: "22" },
  { a: "", b: "", c: 41,d:'', key: "12" },
  { a: "", b: "", c: 25,d:'', key: "3" }
];

const DragColumnTable = dragColumn(Table);

const defaultProps23 = {
  prefixCls: "bee-table"
};

class Demo23 extends Component {
  constructor(props) {
    super(props); 
  }

  render() {
    return <DragColumnTable columns={columns23} data={data23} bordered
    dragborder={true} 
    draggable={true} 
    scroll={{y:200}}
    onDropBorder ={(e,width)=>{
      console.log(width+"--",e.target);
    }}
    />;
  }
}
Demo23.defaultProps = defaultProps23;


export default Demo23;


