class Demo29 extends Component {
  constructor() {
    super();
    this.state = {
      show: false
    }
    this.close = this.close.bind(this);
    this.open = this.open.bind(this);
  }
  handlerFilterChange = (key, val, condition) => {
    console.log('key=', key, ' value=', val, 'condition=', condition);
  }

  handlerFilterClear = (key) => {
    console.log('', key);
  }
  close() {
    this.setState({
      show: false
    });
  }
  open() {
    this.setState({
      show: true
    });
  }
  render() {
    return (<div><Modal
      show={this.state.show}
      onHide={this.close}
      autoFocus={false}
      enforceFocus={false}
    >
      <Modal.Header closeButton>
        <Modal.Title></Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <Table
          onFilterChange={this.handlerFilterChange}//(key,val)=>()
          onFilterClear={this.handlerFilterClear}//(key,val)=>()
          filterDelay={500}//ms300ms
          filterable={true}//
          bordered
          columns={columns29}
          data={data29} />
      </Modal.Body>
    </Modal>
      <Button colors="primary" onClick={this.open}></Button>
    </div>)
  }
}

export default Demo29;


